import { getScenarios, 
        getScenarioAssignmentWithName, 
        publishScenarioAssignment,
        verifyAssignmentName,
        deleteScenarioAssignment } 
from '../scripts/connect-firebase.js';
import { useHistory } from "react-router-dom";
import { useState, useEffect, useRef } from 'react';
import Header from './Header';

const ScenarioAssignment = (props) => {
    const { groupId } = props.match.params;
    const [ scenarios, setScenarios ] = useState(null);
    const [ assignment, setAssignment ] = useState(null);
    const [ originalId, isEdit ] = [ groupId, !!groupId ];
    const groupTitle = useRef(null);
    const checklist = useRef(null);
    const orderRequired = useRef(null);
	let history = useHistory();
    
    useEffect(() => {
        getScenarios()
            .then((result) => { 
                setScenarios(result); 

                groupId ?
                getScenarioAssignmentWithName(groupId)
                    .then((result) => { 
                        setAssignment(result);
                    })
                    .catch((err) => console.log(err)) 
                : setAssignment([]);

            })
            .catch(() => console.log('Scenario loaded error') )
    }, [groupId]);

    const processAssignment = () => {
        let form = checklist.current.children;
        let assignmentObj = {};

        // Get assignments and place in object
        for(let i=0; i < form.length; i++){
            if(form[i].children[0].checked)
                assignmentObj[form[i].children[0].value] = {
                    id: form[i].children[0].value,
                    order: parseInt(form[i].children[1].children[0].value) || null
                };
        }

        console.log(assignmentObj);
        
        return assignmentObj;
    }

    const handleSubmit = async () => {
        let title = groupTitle.current.value;
        
        let assignmentTasks = processAssignment();

        console.log(assignmentTasks);
        
        if ( title.trim() === '' || (await verifyAssignmentName(title) && !isEdit) ) {
            alert('Group needs a title!');
            return;
        }

        (isEdit && originalId !== title) && deleteScenarioAssignment(originalId);

        await publishScenarioAssignment( title, 
            { title: title, orderRequired: orderRequired.current.checked, assignments: assignmentTasks });

        alert(`Published ${groupTitle.current.value}`);
        history.push('/scenario-groups');
    };

    return (
        <>
        <Header />
        <div className="scenario-assignment">
            <h1>Create Scenario Assignment</h1>
            {
                assignment ?
                <>
                <label className="assignment-title">
                    Assignment title: <input type="text" ref={groupTitle} defaultValue={groupId} /> 
                </label>

                <div className="assignment-list" ref={checklist}>
                    {
                        scenarios.map( (item) => (
                            <label key={item.name} className="assignment-check-item">
                                <input type="checkbox" value={item.name} defaultChecked={assignment.assignments && assignment.assignments[item.name] != null}/>
                                {item.name}

                                <span>
                                    # <input type="number" defaultValue={assignment.assignments && assignment.assignments[item.name]?.order}/>
                                </span>
                            </label>
                        ))
                    }
                </div>

                <label className="required-bttn">
                    Order required: <input type="checkbox" ref={orderRequired} defaultChecked={assignment?.orderRequired} />
                </label>
                </>
                : <p>Loading...</p>
            }
            <button type="button" onClick={() => handleSubmit()}>Submit</button>
        </div>
        </>
    );
};

export default ScenarioAssignment;