import { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import { getScenarioAssignments, deleteScenarioAssignment } from '../scripts/connect-firebase.js';
import Header from './Header';

const ScenarioGroupsList = () => {
    const [ groups, setGroups ] = useState(null);
    
    useEffect(() => {
        getScenarioAssignments()
            .then((result) => { setGroups(result); })
            .catch((err) => { console.log(err); })
    }, []);

    return (
        <>
        <Header />
        <div className="scenario-assignment-list">
            <h1>Scenario Assignments</h1>
            <Link to={`/scenario-group/`} className="new-group-bttn scenario-assignment-list-item">
                <p>Create new assignment +</p>
            </Link>
            {
                groups &&
                groups.map( item => (
                    <ListItem item={item} key={item.title} remove={handleDelete} />
                ))
            }
        </div>
        </>
    );

    async function handleDelete(item) {
        let id = item.title;    
        if( window.confirm('Are you sure you want to permanetly delete this group? This cannot be undone!') ){
            deleteScenarioAssignment(id);
            let newGroups = groups.slice();
            newGroups.splice( newGroups.indexOf(item), 1);

            setGroups(newGroups);
        } else
            console.log(`Delete of ${id} cancelled.`);
    }
};

const ListItem = ({ item, remove }) => {
    return (
        <div className="scenario-assignment-list-item">
            <p>{item.title}</p>

            <Link to={`/scenario-group/${item.title}`} className="action-bttn" >Edit</Link>
            <button type="button" className="action-bttn" onClick={() => remove(item)}>Delete</button>
        </div>
    );
}

export default ScenarioGroupsList;