import { db } from "@meddbriefer/mdb-firebase";

export const verifyScenarioName = async (name) => {
    let scenario = await db.collection('scenarios').doc(name).get();
    return scenario.data() != null;
}

export const verifyAssignmentName = async (name) => {
    let scenario = await db.collection('scenario-assignments').doc(name).get();
    return scenario.data() != null;
}

export const publishScenario = async (name, scenarioObject) => {
    await db.collection('scenarios').doc(name).set(scenarioObject)
    alert(`Succesfully published '${name}'.`);
};

export const getScenarios = async () => {
    let collection = await db.collection('scenarios')
                        .where("name", "!=", "blank-trauma") // Prevent edit of blank-trauma
                            .get();
    return collection.docs.map(item => item.data());
};

export const getScenarioWithName = async (name) => {
    let snapshot = await db.collection('scenarios').doc(name).get();
    return snapshot.data();
};

export const permanentlyDeleteScenario = async (name) => {
    await db.collection('scenarios').doc(name).delete();
    console.log(`'${name}' permanently deleted.`);
    return true;
};

export const publishScenarioAssignment = async (name, form) => {
    await db.collection('scenario-assignments').doc(name).set(form);
    console.log(`Grouping '${name}' successfully published.`);
};

export const getScenarioAssignments = async () => {
    let groups = await db.collection('scenario-assignments').get();
    return groups.docs.map(item => item.data());
};

export const getScenarioAssignmentWithName = async (name) => {
    let group = await db.collection('scenario-assignments').doc(name).get();
    return group.data();
};

export const deleteScenarioAssignment = async (name) => {
    await db.collection('scenario-assignments').doc(name).delete();
    console.log(`'${name}' group permanently deleted.`);
};