const ScenarioItem = ({item, scenario}) => {
  return (
    item.type === "heading" 
    ? 
    <ScenarioHeader item={item} />
    :  
    <div className="step-item">
      {item.type === "assessment" 
          ? <Assessment item={item} scenario={scenario} />
          : <Intervention item={item} scenario={scenario} /> }
    </div>
  );
};

const ScenarioHeader = ({item}) => {
  return <div className="list-item header-item">{item.label}</div>;
};

const Intervention = ({item, scenario}) => {
	const intervention = scenario.interventions;
  return (
    <label>
      {`'${item.label}':`}
      <select id={`interventions-${item.id}`}>
    	<option hidden>{intervention[item.id]?.toUpperCase()}</option>
        <option value="na">NA</option>
        <option value="say">SAY</option>
        <option value="do">DO</option>
      </select>
    </label>
  );	
};

const Assessment = ({item, scenario}) => {
	const findings = scenario.assessmentFindings
  return (
    <label>
      {`'${item.label}':`}
      <input
        type="text"
        id={`assessmentFindings-${item.id}`}
        placeholder="leave blank if N/A or unremarkable"
        defaultValue={findings[item.id]}
      />
    </label>
  );
};

export default ScenarioItem;
