import { React, useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { getScenarios, permanentlyDeleteScenario } from '../scripts/connect-firebase';
import Header from './Header';

const ScenariosList = () => {
	const [scenarios, setScenarios] = useState();
	const LOADING = <p>Loading...</p>;
	let listIndex = 0;

	useEffect(() => {
		getScenarios()
			.then((result) => { setScenarios(result) })
			.catch((error) => { console.log(error) })
	}, []);

	return (
		<>
			<Header />

			<div className="scenario-list">
				<h1 id='scenario-list-title'>Scenario List</h1>

				<Link to='/scenario/' className="new-scenario-bttn list-scenario-item">
					<div>
						<h1>+</h1>
						<p>Create a New Scenario</p>
					</div>
				</Link>

				{scenarios 
					? scenarios.map((item) => (
							item && <ListItem item={item} key={`${item.name}-list`} remove={deleteScenario} index={listIndex++} />))
				 	: LOADING }
			</div>
		</>
	);

	async function deleteScenario(index) {
		let id = scenarios[index].name;
		if( window.confirm('Are you sure you want to delete this scenario? This cannot be undone!') ) {
			await permanentlyDeleteScenario(id);
			let newList = scenarios.slice(); // deep copy
			newList.splice(index, 1);
			setScenarios( newList );
		} else {
			console.log('Delete of', id, 'cancelled');
		}
	}
};

const ListItem = ({ item, remove, index }) => {
	return (
		<div className="list-scenario-item">
			<h1 id="title">{item.name}</h1>
			<p>{item.scenarioType}</p>
			<p>Info: {item.info.instructorInformation ?? "Not given."}</p>

			<Link to={`/scenario/${item.name}`} className="action-bttn">Edit</Link>
			<button type="button" className="action-bttn" onClick={() => remove(index) }>Delete</button>
		</div>
	);
};

export default ScenariosList;
