import React, { useState, useEffect, useRef } from "react";
import { useHistory } from "react-router-dom";
import classnames from 'classnames';

import {
	publishScenario,
	getScenarioWithName,
	verifyScenarioName,
	permanentlyDeleteScenario
} from "../scripts/connect-firebase.js";
import * as blankTrauma from '../data/blank-trauma.json';
import BasicInfo from "./BasicInfo.js";
import Steplist from "./Steplist.js";
import Vitals from "./Vitals";
import SAMPLE from "./Sample.js";

/************************ IMPORTANT!! ***************************
   ID's of each HTML input element are very specifically set.
   Do not change or it will break the form's saving and loading.
*****************************************************************/

const ScenarioForm = (props) => {
	const { scenarioId } = props.match.params;
	const [scenario, setScenario] = useState(); // current state of scenario form
	const [activeTab, setActiveTab] = useState('basic-info'); // current view
	const isActiveTab = (name) => name === activeTab;
	const isEdit = !!scenarioId;
	const formEl = useRef(null);
	let history = useHistory();

	// Load and apply selected scenario
	useEffect(() => {
		isEdit ? getScenarioWithName(scenarioId)
			.then((result) => { setScenario(result) })
			.catch((error) => {	console.log(error) })
		: setScenario(blankTrauma.default);
	}, [scenarioId, isEdit]);

	return (
		<div className="scenario-form-wrapper">
			<nav id="tab-selector">
				<div className={classnames("tab-button", {"active-tab": isActiveTab("basic-info")})} 
					 onClick={() => setActiveTab('basic-info')}>Info</div>

				<div className={classnames("tab-button", {"active-tab": isActiveTab("sample")})} 
					 onClick={() => setActiveTab('sample')}>Sample</div>

				<div className={classnames("tab-button", {"active-tab": isActiveTab("vitals")})}
					 onClick={() => setActiveTab('vitals')}>Vitals</div>

				<div className={classnames("tab-button", {"active-tab": isActiveTab("steplist")})} 
					 onClick={() => setActiveTab('steplist')}>Steplist</div>

				<button type="submit" id="submit-next" onClick={() => submitForm()}>Submit â†’</button>
			</nav>

			<form id="scenario-form" className="scenario-form" ref={formEl}>
				{
				scenario ?
				<>
					<BasicInfo 	active={isActiveTab('basic-info')} 	scenario={scenario} edit={isEdit} />
					<SAMPLE 	active={isActiveTab('sample')} 		scenario={scenario} />
					<Vitals 	active={isActiveTab('vitals')} 		scenario={scenario} />
					<Steplist 	active={isActiveTab('steplist')}	scenario={scenario} />
				</>
				: <p>Loading...</p> // while scenario is loading in
				}
			</form>
		</div>
	);

	async function submitForm() {
		let form = formEl.current;
		
		if ( form.name.value == null || form.name.value.trim() === "" || ( await verifyScenarioName(form.name.value) && !isEdit ) ) 
		{
			// If scenario exists and not in edit mode
			alert('Scenario needs a new name!');
		}
		else if (window.confirm("Any empty items will be filled with default information. Submit?"))
		{
			form = processForm(form);
			(isEdit && form.name !== scenario.name) && permanentlyDeleteScenario(scenario.name); // If there's a name change

			publishScenario(form.name, form); // create new
			history.goBack(); // Send back once submitted
		} else 
		{
			console.log("Submit cancelled.");
		}
	}

	function processForm(data) {
		console.log('Processing...');
		let formLength = Object.keys(data).length;

		let input, 
		id, 
		path, 
		newScenario = JSON.parse(JSON.stringify(scenario || {})); // deep copy of original format (for edits)

		for(let i=0; i < formLength; i++ ) {
			if (data[i]?.value) { // Don't process empty fields
				input = data[i].value;
				id = data[i].id;
				path = id.split('-');

				if (path[0] === 'assessmentFindings') path = [path[0], id.split('assessmentFindings-').slice(1).toString()];
				else if (path[0] === 'interventions') path = [path[0], id.split('interventions-').slice(1).toString()];
				// This reconstructs assessmentFindings/interventions paths since id.split('-') breaks them as they include extra hypens.

				// Handle different object depths
				switch (path.length) {
					case 1: newScenario[path[0]] = input; break;
					case 2: newScenario[path[0]][path[1]] = input; break;
					case 3: newScenario[path[0]][path[1]][path[2]] = input; break;

					default: console.log('Path length error:', path.length); break; // Log error
				}
			}
		}

		return newScenario;
	}
};

export default ScenarioForm;
