import React from "react";
import { HashRouter as Router, Switch, Route } from "react-router-dom";

import { AuthProvider, StaffRoute } from "@meddbriefer/mdb-auth";

import "./styles/form-styles.scss";
import "./styles/home-styles.scss";
import "./styles/assignment-styles.scss";

import UnauthedLayout from "./components/UnauthedLayout"

import HomePage from "./components/HomePage";
import ScenarioForm from "./components/ScenarioForm";
import ScenariosList from "./components/ScenariosList";
import ScenarioGroup from './components/ScenarioAssignment';
import ScenarioGroupsList from './components/ScenarioAssignmentsList';


const App = () => {
  return (
    <AuthProvider>
      <Router>
        <Switch>
          <Route exact path="/" component={HomePage} />
          <StaffRoute exact path="/scenarios" authFlowLayout={UnauthedLayout} component={ScenariosList} />
          <StaffRoute exact path="/scenario/" authFlowLayout={UnauthedLayout} component={ScenarioForm} />
          <StaffRoute exact path="/scenario/:scenarioId" authFlowLayout={UnauthedLayout} component={ScenarioForm} />
          <StaffRoute exact path="/scenario-groups" authFlowLayout={UnauthedLayout} component={ScenarioGroupsList} />
          <StaffRoute exact path="/scenario-group/" authFlowLayout={UnauthedLayout} component={ScenarioGroup} />
          <StaffRoute exact path="/scenario-group/:groupId" authFlowLayout={UnauthedLayout} component={ScenarioGroup} />
        </Switch>
      </Router>
    </AuthProvider>
  );
};

export default App;
