import classnames from 'classnames';

const BasicInfo = ({ active, scenario, edit }) => {
	const info = scenario.info;
  const pt = 'patientInformation';
	return (
		<div id="basic-info" className={classnames("info-wrapper", {"scenario-view-hidden": !active})}>
			<h1>Basic Information:</h1>
			<label>
				Scenario Title:
				<input id="name" type="text" defaultValue={edit ? scenario.name : ''} />
			</label>

			<label>
				Instructor Info:
				<textarea id="info-instructorInformation" defaultValue={info.instructorInformation} />
			</label>

			<label>
				Dispatch Info:<textarea id="info-dispatchInfo" defaultValue={info.dispatchInfo} />
			</label>

			<label>
				Scene assessment:<textarea id="info-sceneAssessment" defaultValue={info.sceneAssessment} />
			</label>

			<label>
				Maximum time for scenario:<input id="info-maxTime" type="number" placeholder="minutes" defaultValue={info.maxTime} />
			</label>

			<h2>Patient Info:</h2>
			<div id="pt-info">
				<label>
					Moulage:<input type="text" id={`info-${pt}-moulage`} defaultValue={info.patientInformation.moulage} />
				</label>
				<label>
					Position:<input type="text" id={`info-${pt}-position`} defaultValue={info.patientInformation.position} />
				</label>
				<label>
					Actions:<input type="text" id={`info-${pt}-actions`} defaultValue={info.patientInformation.actions} />
				</label>
			</div>
		</div>
	);
};

export default BasicInfo;
